.row {
  place-items: center;
  justify-content: center; 
  display: flex;
  flex-wrap: wrap;
  margin-top: -10px;
  margin-left: -10px;
  margin-right: -10px;
}
.col,
[class^="col-"],
[class*=" col-"] {
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 10px;
  padding-left: 10px;
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}
.col-12 {
  flex: 0 0 100%;
  max-width: 100%;
}
.col-9 {
  flex: 0 0 65%;
  max-width: 75%;
}
.col-8 {
  flex: 0 0 66.67%;
  max-width: 66.67%;
}
.col-6 {
  flex: 0 0 50%;
  max-width: 50%;
}
.col-4 {
  flex: 0 0 33.33%;
  max-width: 33.33%;
}
.col-3 {
  margin-top: -5px;
  flex: 0 0 25%;
  max-width: 25%;
}

.link {
  stroke: #999;
}

.node {
  stroke: #999;
  stroke-width: 1;
}

/* Map container */
.map-container {
  position: relative;
  border: 1px solid #09131b;
  overflow: hidden;
}

/* Map reset */
#map-reset {
  position: absolute;
  top: 50;
  right: 15px;
  height: 40px;
  padding: 0 20px;
  font-size: 1.6rem;
  font-weight: 600;
  color: #f8fcff;
  background-color: #35a7c2;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 250ms ease, opacity 250ms ease;
}
#map-reset.hidden {
  opacity: 0;
  pointer-events: none;
}
#map-reset:hover,
#map-reset:focus {
  background-color: #207ab5;
}
@media screen and (max-width: 768px) {
  #map-reset {
    top: 0;
    right: 0;
    padding: 0 10px;
  }
}

.sidebar {
  background-color: rgb(35 55 75 / 90%);
  color: #fff;
  padding: 6px 12px;
  font-family: monospace;
  z-index: 1;
  top: 0;
  left: 0;
  margin: 12px;
  border-radius: 4px;
}

.d3-tip {
  line-height: 1;
  padding: 6px;
  background: rgba(240, 240, 240, 0.95);
  color: #333;
  border-radius: 4px;
  font-size: 12px;
}

/* Creates a small triangle extender for the tooltip */
.d3-tip:after {
  box-sizing: border-box;
  display: inline;
  font-size: 10px;
  width: 100%;
  line-height: 1;
  color: rgba(240, 240, 240, 0.95);
  content: "\25BC";
  position: absolute;
  text-align: center;
}

/* Style northward tooltips specifically */
.d3-tip.n:after {
  margin: -2px 0 0 0;
  top: 100%;
  left: 0;
}

.d3-tip h5{
  font-size: 13px;
  font-weight: 600;
  margin: 2px auto;
}


.fixed{
  stroke: rebeccapurple;
  stroke-width: 2 ;

